body {
  background-image: url("/server/public/bground.jpg");
}

.page-container {
  margin: 0 auto;
  width: 1600px;
}

.main-content-container {
  display: flexbox;
  justify-content: center;
  border-radius: 25px;
  margin-top: 40px;
  margin-bottom: 20px;
  box-shadow: 0px 0px 7px 0px white;
  background-color: rgba(235, 239, 231, 0.5);
}

.slogan-image-container {
  display: flex;
  justify-content: center;
}

.slogan-container {
  display: flex;
  justify-content: space-evenly;
  margin-bottom: 10px;
  border-radius: 25px;
  width: 100%;
  height: 350px;
}

.image-container {
  display: flex;
  justify-content: center;
  border: 1px solid purple;
  border-radius: 25px;
  width: 50%;
}

#slogan {
  font-size: 90px;
  font-family: "Shrikhand", cursive;
  color: #390345;
  user-select: none;
  letter-spacing: 5px;
  text-align: center;
  -webkit-text-stroke: 0.5px white;
}

#image {
  width: 50%;
  user-select: none;
}

.app-blurb-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 50px;
  width: 100%;
  height: 400px;
}

.blurb-text {
  align-items: center;
  width: 50%;
  height: 100%;
  font-size: 1.6rem;
  background-color: #D0BBFC;
  border-radius: 400px/200px;
  color: #363636;
  display: flex;
  justify-content: center;
  align-items: center;
}

#about-app {
  margin: 50px;
  font-family: "Cantarell", sans-serif;
  font-weight: 100;
  font-size: 30px;
  text-align: center;
  letter-spacing: 2.5px;
}

.reviews-container {
  display: flex;
  background-color: lightblue;
  align-items: center;
  justify-content: center;
}

.reviews-container div {
  background-color: yellow;
  align-items: center;
  margin: 20px;
  width: 100%;
  height: 100px;
}

.page-container {
  margin: 0 auto;
  width: 1200px;
}

.profile-container {
  display: flex;
}
.profile-column {
  flex: 1 1 100%;
}
.profile-column + .profile-column {
  margin-left: 50px;
}
.profile-card {
  background: rgba(255, 255, 255, 0.5);
  border-radius: 15px;
  padding: 30px;
  font-family: "Cantarell", sans-serif;
  font-weight: 100;
  font-size: 24px;
}
.profile-card + .profile-card {
  margin-top: 50px;
}
.profile-welcome {
  padding-bottom: 20px;
  font-style: italic;
}
.profile-about {
  padding-top: 20px;
  font-style: italic;
}
.profile-skill {
  display: list-item;
  list-style-type: disc;
  list-style-position: inside;
  font-style: italic;
}

h2 {
  font-family: "Shrikhand", cursive;
  font-size: 50px;
}

h4 {
  margin-top: 0;
  margin-bottom: 16px;
  font-size: 30px;
  font-family: "Shrikhand", cursive;
  letter-spacing: 1px;
}

.header-container {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  background-color: #390345;
  font-family: "Shrikhand", cursive;
  text-decoration: none;
  padding-right: 50px;
  top: 100px;
  user-select: none;
}

.header-title {
  color: white;
  padding: 10px;
  letter-spacing: 5px;
  font-weight: 150;
}

.hamburger-container {
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
}

.hamburger {
  font-size: 50px;
  z-index: 2;
  color: #390345;
  margin-bottom: 50px;
}

.hamburger:hover {
  cursor: pointer;
}

.nav-menu {
  margin: 0;
  display: flex;
  align-items: flex-end;
  flex-direction: column;
  position: relative;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
  color: #EBEFE7;
  background-color: #390345;
  border-radius: 10px;
  padding: 2.5px;
}

.main-nav {
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  flex-direction: column;
  position: absolute;
  margin-top: 70px;
  user-select: none;
}

.nav-item {
  text-align: center;
  font-size: 25px;
  font-family: "Chewy", cursive;
  letter-spacing: 4px;
  font-weight: 100;
  margin: 20px;
  line-height: 0.5;
}

.wait-indicator {
  margin: 0 5px 0 5px;
  width: 60px;
  height: 60px;
}